const renderDescription = (type: string, value: string | number) => {
  switch (type) {
    case 'Duration':
      return (
        <>
          <p
            style={{ fontFamily: 'airbnb-book' }}
            className='text-sm text-white'>
            {value} days
          </p>
        </>
      );
    case 'Group size':
      return (
        <>
          <p
            style={{ fontFamily: 'airbnb-book' }}
            className='text-sm text-white'>
            Up to {value} people
          </p>
        </>
      );
    case 'Activity level':
      return (
        <>
          <p
            style={{ fontFamily: 'airbnb-book' }}
            className='text-sm text-white'>
            {value}
          </p>
        </>
      );
    case 'Hosted in':
      return (
        <>
          <p
            style={{ fontFamily: 'airbnb-book' }}
            className='text-sm text-white'>
            {value}
          </p>
        </>
      );
  }
};

interface Props {
  type: string;
  value: number | string;
}

export const BannerCard: React.FC<Props> = ({ type, value }) => {
  return (
    <div className='w-1/2'>
      <div className='h-4 w-4 my-2'>{renderIcon(type)}</div>
      <p
        style={{ fontFamily: 'airbnb-book' }}
        className='text-sm text-gray-500'>
        {type}
      </p>

      {renderDescription(type, value)}
    </div>
  );
};
